
import XHeader from 'vux/src/components/x-header/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import ScrollAll from '../../components/scrollAll.vue'
import {mapMutations, mapState} from 'vuex'
import {refreshMixin} from '../../common/js/mixin'
import NoData from '../../components/noData'
import Scroll from '../../components/scrollAll'

export default {
name: "chooseProduct",
mixins: [refreshMixin],
components: {
ScrollAll,
XHeader,
Checklist,
XButton,
NoData,
Scroll
},
data() {
return {
commonList: [],
radioValue: [],
list: [],
page: 1,
count: 20,
pullUp: {
threshold: 0,
txt: {
more: "加载更多",
noMore: "没有更多了"
}
},
pullDown: {
threshold: 50,
stop: 20
},
loading: false
}
},
computed: {
...mapState(['chooseProductData'])
},
mounted() {
this.loading = true
this.GetCompCustomerList()
this.$refs.content.addEventListener('scroll', (e) => {
if (e.target.scrollHeight - e.target.scrollTop - e.target.offsetHeight < 50) {
this.page++
if (!this.loading) {
this.loading = true
}
this.GetCompCustomerList()
}
})
},
methods: {
...mapMutations(['setChooseProduct']),
goBack() {
this.refresh('changeOrder', false)
this.$router.back()
},
change() {
},
GetCompCustomerList() {



axios.get(`/CrmProd/GetPagedCrmProd?page=${this.page}&count=${this.count}`).then(res => {
this.loading = false
this.$nextTick().then(() => {
this.$refs.scroll.reSize()
})
if (res.data.State === 1) {

if(this.page === 1){
this.commonList = []
this.list = res.data.Data.List
res.data.Data.List.forEach(item => {
this.commonList.push({
value: item.ProdName,
key: item.ID
})
})
this.$refs.scroll.scroll.finishPullDown();
}else{
this.list.push(...res.data.Data.List)
res.data.Data.List.forEach(item => {
this.commonList.push({
value: item.ProdName,
key: item.ID
})
})
this.$refs.scroll.scroll.finishPullUp();
}
this.radioValue = []
if (this.chooseProductData.length !== 0) {
this.chooseProductData.forEach(item => {
this.radioValue.push(item.ID)
})
}
this.$refs.scroll.forceUpdate();
this.$refs.scroll.refresh();
} else {
this.$refs.scroll.scroll.finishPullDown();
this.$refs.scroll.forceUpdate();
this.$vux.toast.show({
text: res.data.ErrorMessage,
type: 'warn'
})
}
}).catch(e => {
console.log(e)
this.$refs.scroll.scroll.finishPullDown();
this.$refs.scroll.forceUpdate();
this.loading = false
this.$vux.toast.show({
text: '网络异常',
type: 'cancel'
})
})
},
confirm() {
let data = []
this.radioValue.forEach((item, index) => {
this.list.forEach((item1, index1) => {
if (item === item1.ID) {
this.$set(this.list[index1], 'DealNum', 1)
this.$set(this.list[index1], 'Remark', '')
this.$set(this.list[index1], 'DealPrice', item1.ProdPrice)
data.push(item1)
}
})
})
if (data.length == 0 && this.list.length != 0) {
this.$vux.toast.show({
text: '请选择产品',
type: 'warn'
})
return
}
let data2 = this.$store.state.chooseProductData
data2.forEach(item=>{
data.forEach((item2,index)=>{
if(item.ID == item2.ID){
data[index].DealNum = item.DealNum
}
})
})
this.setChooseProduct(data)
this.refresh('changeOrder', false)
this.$router.back()
},
getData(status) {
console.log(1)
if (status === 1) {
this.page = 1
} else {
this.page++
}
this.GetCompCustomerList()
}
}
}



